.brief {
	text-align: center;
	max-width: 45rem;
	width: 90%;
	margin: auto;
	margin-top: -10rem;
	position: relative;
	background-color: #383838;
	color: white;
	border-radius: 14px;
	padding: 1rem;
	box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
	animation: brief-appear 1s ease-out forwards;
}

.brief h2 {
	font-size: 2rem;
	margin-top: 0;
}

@keyframes brief-appear {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}