.experiences {
	max-width: 60rem;
	width: 90%;
	margin: 2rem auto;
	animation: experiences-appear 1.5s ease-out forwards;
}

.experiences ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

@keyframes experiences-appear {
	from {
		opacity: 0;
		transform: translateY(-3rem);
	}

	to {
		opacity: 1;
		transform: translateY(0rem);
	}
}